<template>
	<div class="wrapper ">
		<div class="container">
			<div class="row justify-content-center">
				<div class="col-12 col-md-5 col-xl-4 my-5">
					<div v-if="errors.length">
						<div v-for="error in errors" :key="error.id" class="alert alert-danger fade show text-center" role="alert">
							{{ error['msg'] }}
                 		</div>
					</div>
					<div class="card">
						<div class="card-body">
							<h1 class="display-4 text-center mb-3">Rage</h1>
							<p class="text-muted text-center mb-5">
								Enter your password to sign in.
							</p>
							<form id="login-form" v-on:submit.prevent="processLogin" method="post">
								<div class="form-group">
									<label>Username</label>
									<input type="text" class="form-control" value="Cloud" disabled>
								</div>
								<div class="form-group">
									<div class="row">
										<div class="col">
											<label>Password</label>
										</div>
										<div class="col-auto">
											<a href="password-reset.html" class="form-text small text-muted">
												Forgot password?
											</a>
										</div>
									</div> 
									<div class="input-group input-group-merge">
										<input type="password" name="password" v-model="password" class="form-control form-control-appended" placeholder="Enter your password">
										<div class="input-group-append">
											<span class="input-group-text">
												<i class="fe fe-eye"></i>
											</span>
										</div>
									</div>
								</div>
								<button class="btn btn-lg btn-block btn-primary mb-3">Sign in</button>
							</form>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				password: '',
				success: '',
				errors: []
			}
		},

		methods: {
			processLogin: function(e) {
				this.errors = [];
				this.success = '';

				//if all inputs have been filled
				if(this.password) {
					mp.trigger('sendData', JSON.stringify({ password: this.password, action: 'login' }));
				}

				// if the password isn't filled
				if(!this.password) {
					this.errors.push(new Object({ 'msg': 'A password is required.'}));
				}

				//this.$forceUpdate();

				e.preventDefault();
			}
		}
	}
</script>

<style lang="scss" scoped>
	
</style>